/**
 * @desc class
 * @param {ele} dom 
 * @param {cls} className
 * @return {Boolean}
 */
export const hasClass = (ele, cls) => {
    return (new RegExp('(\\s|^)' + cls + '(\\s|$)')).test(ele.className);
}

/**
 * @desc className
 * @param {ele} dom 
 * @param {cls} className
 * @return {String}
 */
export const addClass = (ele, cls) => {
    if (!hasClass(ele, cls)) {
        ele.className += ' ' + cls;
    }
}

/**
 * @desc className
 * @param {ele} dom 
 * @param {cls} className
 * @return {String}
 */
export const removeClass = (ele, cls) => {
    if (hasClass(ele, cls)) {
        var reg = new RegExp('(\\s|^)' + cls + '(\\s|$)');
        ele.className = ele.className.replace(reg, ' ');
    }
}
 