export const isInteger = (num) => {
    return Math.floor(num) == num;
}

/**
 * @desc 
 * @param {String} str
 * @return  {Boolean}
 * **/
export const checkNumber = (theObj) => {
    const reg = /^[0-9]+.?[0-9]*$/;
    if (reg.test(theObj)) {
      return true;
    }
    return false;
}








