/**
 * @desc 31
 * @param  {str} 
 * @return {str}     
 */
export const cutStr = (str) => {
    let newStr = new Array(str.length + parseInt(str.length/3));
    newStr[newStr.length-1] = str[str.length-1];
    let currentIndex=str.length-1; 
    for(let i=newStr.length-1;i>0;i--) {
        if((newStr.length-1)%4 == 0) {
            newStr[i]=",";
        }else {
            newStr[i] = str[currentIndex-1]
        }
    }
    return newStr.join("");
}

/**
 * @desc 
 * @param  {num} 
 * @return {num}     
 */
export const getRandomNum = (count) => {
    let arr = new Array;
	let reNum = "";
	for(let i=0;i<count;i++){
		arr[i] = parseInt(Math.random()*10);
		reNum += String(arr[i]);
	}
	return reNum;
}

/**
 * @desc 
 * @param  {num} 
 * @return {num}     
 */
export const random = (min,max) => {
	return Math.floor(min+Math.random()*(max-min));
}

/**
 * (,,)
 * @param {Number} n 
 * @return  {String}
 */
export const smalltoBIG = (n = 0) => {
    var fraction = ['', ''];
    var digit = ['', '', '', '', '', '', '', '', '', ''];
    var unit = [ ['', '', '',''], ['', '', '', '','']  ];
    var head = n < 0? '': '';
    n = Math.abs(n);
  
    var s = '';
  
    for (var i = 0; i < fraction.length; i++)
    {
      s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
    }
    s = s || '';
    n = Math.floor(n);
  
    for (var i = 0; i < unit[0].length && n > 0; i++)
    {
      var p = '';
      for (var j = 0; j < unit[1].length && n > 0; j++)
      {
        p = digit[n % 10] + unit[1][j] + p;
        n = Math.floor(n / 10);
      }
      s = p.replace(/(.)*$/, '').replace(/^$/, '')  + unit[0][i] + s;
    }
    return head + s.replace(/(.)*/, '').replace(/(.)+/g, '').replace(/^$/, '');
  }