/**
 * @desc 
 * @param {Object} obj 
 * @return {Boolean}
 */
export const isEmptyObj = (obj = {}) => {
    return Object.keys(obj).length ? false : true
}

/**
 * @desc 
 * @param {Array} arr 
 * @return {Boolean}
 */
export const isEmptyArr = (arr = []) => {
    return arr.length ? false : true
}









