/**
 * @desc 2
 * chunk(['a', 'b', 'c', 'd'], 3) => [['a', 'b', 'c'], ['d']]
 * @param  {array} 
 * @param  {size} 
 * @return {array} 
 */
export const chunk = (array, size) => {
    size = Math.max(size, 0);
    const length = array == null ? 0 : array.length;
    if(!length || size < 1) {
        return []
    }
    let index = 0;
    let resIndex = 0;
    const result = new Array(Math.ceil(length/size));
    while(index < length) {
        result[resIndex++] = array.slice(index, index += size)
    }
    return result;
}

/**
 * @desc false
 * compact([0, 1, false, 2, '', 3]) => [1, 2, 3]
 * @param  {array} 
 * @return {array} 
 */
export const compact = (array) => {
    if(array == null) return ;
    const transArr = array.filter( (item) => {
        return !!item
    });
    return transArr;
}

/**
 * @desc 
 */
export const dedupe = (array = []) => {
    return Array.from(new Set(array));
}

/**
 * @desc 
 * @param  {array} 
 * @param  {sortFlag}  
 * @return   {array} 
 */
const _numAscSort = (a, b) => {
    return a - b;
}

const _numDescSort = (a, b) => {
    return b-a;
}

export const orderBy = (arr = [], sortFlag) => {
    let $arr = arr;
    if(sortFlag === 'asc') {
        $arr.sort(_numDescSort);
    }else {
        $arr.sort(_numAscSort);
    }
    return $arr;
}

/**
 * @desc 
 * @param  {array} 
 * @param  {array}
 * @return   {array} 
 */
export const union = (a, b) => {
    let newArr = a.concat(b);
    return dedupe(newArr);
}

/**
 * @desc 
 */
export const complement = (a, b) => {
    return minus(union(a, b), intersect(a, b));
}

 /**
 * @desc 
 */
export const interset = (a, b) => {
    a = dedupe(a);
    return a.map( (item) => {
        return b.includes(item) ? item : null;
    })
}

 /**
 * @desc 
 */
export const minus = (a, b) => {
    a = dedupe(a);
    return a.map( (item) => {
        return b.includes(item) ? null : item;
    })
}

 /**
 * @desc 
 */
export const unique = (arr=[]) => {
    for(let i=0;i<arr.length;i++) {
        for(let j=i+1;j<arr.length;) {
            if(arr[j] === arr[i]) {
                arr.splice(i, 1);
            }else {
                j++;
            }
        }
    }
    return arr;
}

 /**
 * @desc ()
 */
export const uniqueByObj = (arr = []) => {
    let result = [],
        hash = {};
    for(let i=0, elem; (elem = arr[i]) != null; i++) {
        if(!hash[elem]) {
            result.push(elem);
            hash[elem] = true;
        }
    }
    return result;
}

 /**
 * @desc 
 */
export const indexof = (arr = [], val) => {
    return arr.indexOf(val)
}

 /**
 * @desc 
 */
export const removeEle = (arr = [], indexs) => {
    let index = indexof(arr, indexs);
    if(index > -1) {
        arr.splice(index, 1);
    }
    return arr;
}

